.home{
  position: relative;

  .totalMinerWrap{
    width: 17%;
    position: absolute;
    top: 5%;
    left: 3%;
    img{
      width: 100%;
    }

    .contentWrap{
      width: 90%;
      position: absolute;
      top: 8%;
      left: 5%;

      h2{
        font-size: 1.3vw;
        width: 30%;
        color: black;
        font-weight: bold;
      }

      p{
        position: absolute;
        left: 60%;
        bottom: 10%;
        transform: translateX(-50%);
        color: black;
        font-weight: bold;
        font-size: 1.2vw;
      }
    }
  }

  .textLogo{
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 33%;
  }

  .walletBalanceWrap{
    position: absolute;
    top: 5%;
    right: 3%;
    width: 17%;

    img{
      width: 100%;
    }
    .contentWrap{
      width: 70%;
      position: absolute;
      top: 59%;
      transform: translateY(-50%);
      left: 20%;

      h2{
        font-size: 1vw;
        color: black;
        font-weight: bold;
      }

      p{
        color: black;
        text-align: center;
        font-weight: bold;
        font-size: 1.2vw;
      }
    }
  }

  .halfWrap{
    position: absolute;
    top: 19%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    h2{
      font-size: 16px;
      color: black;
      text-align: center;
    }
  }

  .socialMediaWrap{
    position: absolute;
    bottom: 5%;
    left: 5%;
    width: 40%;

    img, div{
      display: inline-block;
      vertical-align: middle;
      color: white;
      margin: 0 15px;
    }
    img{
      width: 40px;
      border-radius: 20px;
    }

    div{
      .selected{
        color: #edb58c;
        font-weight: bold;
      }
    }
  }

  .miner{
    position: absolute;
    bottom: 20%;
    left: 13%;
    width: 13%;
  }

  .mineWrap{
    position: absolute;
    bottom: 18%;
    left: 50%;
    transform: translateX(-50%);
    width: 32%;
    img{
      width: 100%;
    }
    .mobileMineWrap{
      display: none;
    }
    .contentWrap{
      width: 80%;
      position: absolute;
      top: 7%;
      left: 10%;

      h3{
        text-align: left;
        font-size: 1.3vw;
      }

      h1{
        font-size: 2.1vw;
        font-weight: 600;
      }

      h2{
        font-size: 1.3vw;
        font-weight: 600;
      }

      .countdownWrap{

      }

      .buttonGroup{
        .button{
          display: inline-block;
        }

        .buttonDisabled{
          background-color: grey;
        }

      }

      .button{
        margin: 10px;
        padding: 7px 0;
        width: 30%;
        border-radius: 10px;
        font-size: 1.1vw;
        font-weight: 600;
      }

      .boostCountdownWrap{
        margin-bottom: 5px;
        color: white;
        font-size: 1.1vw;
      }

      p{
        width: 100%;
        font-size: 1vw;
      }
    }
  }

  .referralWarp{
    position: absolute;
    bottom: 22%;
    right: 13%;
    width: 15%;
    img{
      width: 100%;
    }
    .contentWrap{
      position: absolute;
      top: 18%;
      left: 20%;
      width: 80%;
      h2{
        font-size: 1.2vw;
        color: black;
      }

      p{
        margin-top: 45%;
        text-align: center;
        font-size: 1.3vw;
        color: black;
        padding-right: 5%;
      }

      .button{
        width: 70%;
        margin: 5px auto -5% auto;
        background: #643A24;
        box-shadow: 0 3px 6px #000000;
        padding: 5px 0;
        text-align: center;
        border-radius: 5px;
        font-size: 1.1vw;
      }
    }
  }

  .miningRulesWrap{
    right: initial !important;
    left: 27%;
  }

  .dividendsWrap{
    right: initial !important;
    left: 3%;
  }

  .referralRulesWrap{
    z-index: 2;
    position: absolute;
    bottom: 22%;
    right: 3%;
    width: 7% !important;
    background: none !important;
    box-shadow: none !important;
    margin: 0 !important;
    img{
      width: 100%;
    }
    .contentWrap{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: 50%;
      font-size: 1.3vw;
      color: #4D2F0E;
    }
  }

  .pancakeCountdownWrap{
    position: absolute;
    bottom: 4%;
    left: 50%;
    transform: translateX(-50%);
    text-align:center;

    h2{
      font-size: 1vw;
    }

    .buttonGroups{
      margin-top: 10px;
      a{
        color: black;
        background: #FFD3B3;
        height: 40px;
        display: inline-block;
        line-height: 40px;
        border-radius: 10px;
        padding:  0 20px;
        margin: 0 10px;
        font-size: 16px;
        img{
          height: 30px;
          margin-right: 10px;
        }
      }
    }
  }

  .pinkSale{
    position: absolute;
    bottom: 4%;
    left: 50%;
    transform: translateX(-50%);
    text-align:center;
    padding: 10px 20px;
    width: 150px !important;
    font-size: 18px !important;
    font-weight: bold !important;
    background-color: #f95192 !important;
  }

  @media(max-width: 900px){
    .totalMinerWrap{
      width: 40%;
      top: 2%;

      .contentWrap{
        h2{
          font-size: 1.4vh;
        }

        p{
          bottom: 0;
          font-size: 1.6vh;
        }
      }
    }

    .textLogo{
      top: 10%;
      width: 80%;
    }

    .walletBalanceWrap{
      width: 40%;
      top: 2%;

      .contentWrap{

        h2{
          font-size: 1.3vh;
        }

        p{
          font-size: 1.5vh;
        }
      }
    }

    .halfWrap{
      top: 17%;

      h2{
        font-size: 14px;
      }
    }

    .socialMediaWrap{
      bottom: 3%;
      left: 0;
      width: 100%;
      img{
        width: 35px;
        margin: 0 10px;
      }

      div{
      }
    }

    .miner{
      bottom: 12%;
      left: 17%;
      width: 15%;
    }

    .mineWrap{
      bottom: initial;
      top: 25%;
      width: 90%;

      img{
        display: none;
      }
      .mobileMineWrap{
        display: block;
      }

      .contentWrap{
        width: 80%;
        position: absolute;
        top: 7%;
        left: 10%;

        h3{
          font-size: 1.8vh;
        }

        h1{
          font-size: 2.5vh;
        }

        h2{
          font-size: 1.7vh;
        }

        .button{
          font-size: 1.5vh;
          padding: 5px 0;
          margin: 5px 10px;
          width: 35%;
        }

        .boostCountdownWrap{
          font-size: 1.5vh;
        }
        p{
          font-size: 1.3vh;
        }
      }
    }

    .referralWarp{
      position: absolute;
      bottom: 12%;
      right: 16%;
      width: 43%;
      img{
        width: 100%;
      }
      .contentWrap{
        position: absolute;
        top: 18%;
        left: 20%;
        width: 80%;
        h2{
          font-size: 1.6vh;
        }

        p{
          margin-top: 50%;
          font-size: 1.5vh;

        }

        .button{
          margin: 5px auto -10% auto;
          font-size: 1.3vh;
        }
      }
    }

    .miningRulesWrap{
      left: 33% !important;
    }

    .dividendsWrap{
      left: 1% !important;
    }

    .referralRulesWrap{
      bottom: 12% !important;
      right: 2%;
      width: 17% !important;

      .contentWrap{
        font-size: 1.5vh;
      }
    }

    .pancakeCountdownWrap{
      bottom: 39%;

      h2{
        font-size: 1.4vh;
        color: black;
        span, strong{
          color: black;
        }
      }
      .addressInfo{
        span{
          padding-right: 5px;
          display: inline-block;
          width: auto;
          color: black;
          vertical-align: middle;

        }

        .address{
          width: 70%;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow-x: hidden;
        }
      }

      .buttonGroups{
        margin-top: 10px;
        a{
          height: 30px;
          line-height: 30px;
          font-size: 14px;
          img{
            height: 20px;
          }
        }
      }
    }

    .pinkSale{
      bottom: 40%;
    }
  }
}
